const fontSize = [150, 130, 110, 97, 84, 74, 67, 60, 56, 52, 48];
let file = '';

function init(_el) {
    const canvas = document.getElementById(_el);
    const ctx    = canvas.getContext('2d');

    return {
        canvas, ctx,
        fn_loadImage,
        fn_fillText
    };
}

function fn_setViewCanvas(img) {
    const canvas = document.getElementById('viewCanvas');
    const ctx = canvas.getContext('2d');
    const imageDiv = document.querySelector('.image');

    const imgPer = imageDiv.clientWidth / img.width * 100;

    canvas.width  = img.width  * imgPer / 100;
    canvas.height = img.height * imgPer / 100;

    ctx.drawImage(img, 0, 0, canvas.width, canvas.height);
}

function fn_fillText(obj, val) {
    fn_loadImage(obj).then(() => {
        if (val) {
            if ((val.length - 5) >= 0) {
                obj.ctx.font = `${fontSize[val.length - 5]}px mono`;
            } else {
                obj.ctx.font = `${fontSize[0]}px mono`;
            }
            
            obj.ctx.fillText(val, 500, 300);

            const img = new Image();
            img.src = obj.canvas.toDataURL();
            img.onload = () => {
                fn_setViewCanvas(img);
            }
        }
    });
}

function fn_loadImage(obj, imgFile) {
    if (imgFile) {
        file = imgFile;
    }

    return new Promise((resolve) => {
        const canvas = obj.canvas;
        const ctx = obj.ctx;
        const img = new Image();

        img.src = file;
        img.onload = () => {
            canvas.width = img.width;
            canvas.height = img.height;
            ctx.drawImage(img, 0, 0);
            fn_setViewCanvas(img);
            resolve();
        }
    });
}

module.exports = {
    init,
    fn_setViewCanvas,
    fn_fillText,
    fn_loadImage
}