<template>
  <div>
    <table>
      <colgroup>
        <col style="width: 18%;">
        <col>
      </colgroup>

      <tbody>
        <tr>
          <td colspan="2">
            <div class="image">
              <canvas id="viewCanvas"></canvas>
            </div>
          </td>
        </tr>
        
        <tr>
          <td>넣을 내용 : </td>
          <td><input type="text" @input="fn_setText"></td>
        </tr>
      </tbody>
    </table>
  </div>

  <canvas id="resultCanvas"></canvas>

  <div class="btn-wrap">
    <!-- <button @click="fn_download()">다운로드</button> -->
    <button @click="fn_download()">다운로드</button>
  </div>
</template>

<script>
import CanvasJS from '@/js/canvas.js';
import imgFile from '@/assets/template/template_2.png'

export default {
  name: 'CoverComponent',
  props: {},
  data() {
    return {
      imgCanvas: '',
      resultCanvas: ''
    };
  },
  mounted() {
    // this.imgCanvas = CanvasJS.init('viewCanvas');
    this.imgCanvas = CanvasJS.init('resultCanvas');
    this.imgCanvas.fn_loadImage(this.imgCanvas, imgFile);
  },
  methods: {
    fn_setText(_val) {
      this.imgCanvas.fn_fillText(this.imgCanvas, _val.target.value);
    },

    fn_download() {
      const a = document.createElement('a');
      a.href = this.imgCanvas.canvas.toDataURL();
      a.download = 'result.png';
      a.click();
      a.remove();
    }
  }
}
</script>

